.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

header nav ul{
  display:flex;
  list-style: none;
  padding: 0px;
  margin: 0px;
  align-items: center;
}

header nav ul li a{
  display:inline-flex;
  padding: 1rem 1.5rem;
  text-decoration: none;
  color:#fff;
}
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px;
  padding: 0.8rem;
  background-color: #cf2c6b;
  color: #fff;
}


*{
  margin: 0;
  padding: 0;
  font-family: "Open Sans",sans-serif;
  box-sizing: border-box;
}

section.conocenos{
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Open Sans",sans-serif;
}

.about-section{
  background: url('https://mayra304186554.files.wordpress.com/2021/03/back4.jpg') no-repeat left;
  background-size: 55%;
  background-color:#d8d0d0;
  overflow: hidden;
  padding: 100px 0;
}

.inner-container{
  width: 55%;
  float: right;
  background-color: #fdfdfd;
  padding: 150px;
}

.inner-container h1{
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 900;
}

.text{
  font-size: 13px;
  color: #545454;
  line-height: 30px;
  text-align: justify;
  margin-bottom: 40px;
}

.skills{
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 13px;
}

@media screen and (max-width:1200px){
  .inner-container{
    padding: 80px;
  }
}

@media screen and (max-width:1000px){
  .about-section{
    background-size: 100%;
    padding: 100px 40px;
  }

  .inner-container{
    width: 100%;
  }
}

@media screen and (max-width:600px){
  .about-section{
    padding: 0;
  }

  .inner-container{
    padding: 60px;
  }
}

section.paralax {
  background-image: url('https://mayra304186554.files.wordpress.com/2021/03/paralax.jpg');
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
  min-width:100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

section.paralax h2{
  font-size:2rem;
  color:#fff;
  padding:1em;
  text-align: center;
  text-shadow: 0px 0px 7px #545454;
}


.striked{
  text-decoration: underline;
}
